import React,{useState,useEffect} from 'react';
import articles from './article-content';
import ArticleList from '../components/ArticleList';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSelction';
import AddCommentForm from '../components/AddCommentForm';
import NotFoundPage from '../Pages/NotFoundPage';

const ArticlePage=({match})=>{
    const [articleInfo,setArticleInfo]=new useState({upvotes:0,comments:[]});
    const paramName=match.params.name;
      const myarticle = articles.find(a=>a.name===paramName);
    const otherArticles=articles.filter(a=>a.name!==paramName);
useEffect   (
    ()=>{
        const fetchData=async()=>{
        const result=await  fetch(`/api/articles/${paramName}`);
        const body=await result.json();
        setArticleInfo(body);
        }
        fetchData();
    },[name]);
    
  if (myarticle ===undefined)
   return( <NotFoundPage/>);
  return (
      <React.Fragment>
      <h1>{myarticle.title}</h1>
      <UpvotesSection name={paramName} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
      {myarticle.content.map((paragraph, key) => (
          <p key={key}>{paragraph}</p>
      ))}
       <CommentsList comments={articleInfo.comments} />
       <AddCommentForm articleName={paramName} setArticleInfo={setArticleInfo} />
      <h3>Other Articles</h3>
      <ArticleList articles={otherArticles}/>
      </React.Fragment>
  );
}
export default ArticlePage;


